import React, { useState, useEffect } from "react";
import {
  Formik,
  Field,
  Form,
  ErrorMessage,
  setNestedObjectValues,
  FormikHelpers,
  FieldArray,
} from "formik";

import * as Yup from "yup";

//import { transactionout } from "../services/product.service";

import * as AuthService from "../services/auth.service";

import { getModeratorBoard } from "../services/user.service";
import EventBus from "../common/EventBus";

import {
  getProductList,
  getAvailableList,
  getStandList,
} from "../services/product.service";

const TransactionOut: React.FC = () => {
  const [showModeratorBoard, setShowModeratorBoard] = useState<boolean>(false);
  const [content, setContent] = useState<string>("");
  const [successful, setSuccessful] = useState<boolean>(false);
  const [loading, setLoading] = useState<boolean>(false);
  const [message, setMessage] = useState<string>("");
  const [standIdentSuccess, setStandIdentSuccess] = useState<boolean>(false);
  const [standIdent, setStandIdent] = useState<string>("");
  const [standName, setStandName] = useState<string>("");
  const [productAvailable, setProductAvailable] = useState<any[]>([]);
  const [productList, setProductList] = useState<any[]>([]);
  const [standList, setStandList] = useState<any[]>([]);

  const initialValuesS1: {
    standIdent: "";
    transactions: [
      {
        product: "";
        quantity: 0;
      }
    ];
  } = {
    standIdent: "",
    transactions: [
      {
        product: "",
        quantity: 0,
      },
    ],
  };

  const handleTransaction = (formValue: {
    standIdent: string;
    transactions: Array<any>;
  }) => {
    setMessage("");
    setLoading(true);

    const { standIdent, transactions } = formValue;

    setStandIdent(standIdent);

    if (!standIdentSuccess) {
      getStandList().then(
        (response) => {
          setStandList(response.data.standList);
        },
        (error) => {
          const resMessage =
            (error.response &&
              error.response.data &&
              error.response.data.message) ||
            error.message ||
            error.toString();

          setLoading(false);
          setMessage(resMessage);

          if (error.response && error.response.status === 401) {
            EventBus.dispatch("logout");
          }
        }
      );
    }

    /*transactionout(product, quantity, stand, user.username, type).then(
      (response) => {
        setMessage(response.data.message);
        setSuccessful(true);
      },
      (error) => {
        const resMessage =
          (error.response &&
            error.response.data &&
            error.response.data.message) ||
          error.message ||
          error.toString();

        setMessage(resMessage);
        setSuccessful(false);
      }
    );*/
  };
  useEffect(() => {
    const fetchStandName = async () => {
      try {
        for (let i = 0; i < standList.length; i++) {
          if (standList[i].identity === standIdent) {
            setStandName(standList[i].name);
            setStandIdentSuccess(true); // set standIdentSuccess to true         
          }
          if (standName !== "") {
            console.log("Barraca identificada:", standName);
            setLoading(false);
            setMessage("");
          }else{
            setMessage("Barraca no identificada!");}
            setLoading(false);
        }
      } catch (error) {
        console.error("Barraca no identificada!", error);
        setMessage("Barraca no identificada!");
        setLoading(false);
      }
    };

    fetchStandName();
  }, [standList]);

  useEffect(() => {
    const user = AuthService.getCurrentUser();

    if (user) {
      setShowModeratorBoard(user.roles.includes("ROLE_MODERATOR"));
    }

    getModeratorBoard().then(
      (response) => {
        setContent(response.data);
      },
      (error) => {
        const _content =
          (error.response &&
            error.response.data &&
            error.response.data.message) ||
          error.message ||
          error.toString();

        setContent(_content);

        if (error.response && error.response.status === 401) {
          EventBus.dispatch("logout");
        }
      }
    );
  }, []);

  return (
    <>
      <div className="card col-8">
        <h3 className="card-header">Sada de Produto</h3>
        <div className="card-body">
          {!standIdentSuccess && (
            <>
              <h4 className="card-title">Idenfique a Barraca</h4>
              <Formik
                initialValues={initialValuesS1}
                onSubmit={handleTransaction}
              >
                <Form>
                  <div>
                    <div className="form-group">
                      <label htmlFor="standIdent">
                        Digite a idenficao da Barraca
                      </label>
                      <Field
                        type="text"
                        name="standIdent"
                        className="form-control"
                      />
                    </div>
                    <div className="d-grid gap-2 d-md-flex col-3 mx-auto justify-content-md-end">
                      <button
                        type="submit"
                        className="btn btn-primary btn-block btn-sm"
                        disabled={loading}
                      >
                        {loading && (
                          <span className="spinner-border spinner-border-sm"></span>
                        )}
                        <span>Prximo</span>
                      </button>
                      {message && (
                        <div className="form-group">
                          <div
                            className={
                              successful
                                ? "alert alert-success"
                                : "alert alert-danger"
                            }
                            role="alert"
                          >
                            {message}
                          </div>
                        </div>
                      )}
                    </div>
                  </div>
                </Form>
              </Formik>
            </>
          )}
          {standIdentSuccess && (
            <>
              <h4 className="card-title">Liste os Produtos para a barraca {standName}</h4>
              <Formik
                initialValues={initialValuesS1}
                onSubmit={handleTransaction}
              >
                <Form>
                  <div>
                    <div>
                      {message && (
                        <div className="form-group">
                          <div
                            className={
                              successful
                                ? "alert alert-success"
                                : "alert alert-danger"
                            }
                            role="alert"
                          >
                            {message}
                          </div>
                        </div>
                      )}
                    </div>
                  </div>
                </Form>
              </Formik>
            </>
          )}
        </div>
      </div>
    </>
  );
};

export default TransactionOut;
